import {TabBar} from 'antd-mobile'
import { NavLink } from 'react-router-dom';
import React from 'react';
import './goods.css'

class Goods extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          itemIndex:0,
        };
    }
    render(){
        return(
        <div className='goods'>
		  <div className='content'>
			<img src={'https://gw.alicdn.com/bao/uploaded/i3/TB1a607PpXXXXXyXVXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp'} />
			<NavLink to='/'>
			<div className='fanhui'>
				<i className='iconfont icon-fanhui' style={{color:'white',position:'fixed',fontSize:'20px'}}></i>
			</div>
			</NavLink>
			<div className='gowuche'><i className='iconfont icon-gouwuche1' style={{color:'white',position:'fixed',fontSize:'28px'}}></i></div>
		  </div>
		  <div className='bar'>
			<div className='bar_tabbar'>
				<TabBar unselectedTintColor="#949494" tintColor="#FF4E22" barTintColor="white">
					<TabBar.Item 
					title="店铺"
					key="dianpu"
					icon={
						<i className='iconfont icon-dianpu'></i>
					}
					selectedIcon={
						<i className='iconfont icon-dianpu'></i>
					}
					selected={this.state.itemIndex === 0}
					onPress={() => {
						this.setState({itemIndex:0,});
					}}
					data-seed="logId"
					>
					</TabBar.Item>
					<TabBar.Item
					icon={
						<i className='iconfont icon-kefu'></i>
					}
					selectedIcon={
						<i className='iconfont icon-kefu'></i>
					}
					title="客服"
					key="shop"
					selected={this.state.itemIndex === 1}
					onPress={() => {
						this.setState({itemIndex:1,});
					}}
					data-seed="logId1"
					>
					</TabBar.Item>
					<TabBar.Item
					icon={
						<i className='iconfont icon-shoucang'></i>
					}
					selectedIcon={
						<i className='iconfont icon-shoucang'></i>
					}
					title="收藏"
					key="list"
					selected={this.state.itemIndex === 2}
					onPress={() => {
						this.setState({itemIndex:2,});
					}}
					data-seed="logId2"
					>
					</TabBar.Item>
				</TabBar>
			</div>
			<div className='bar_buy'>加入购物车</div>
			<div className='bar_join'>立即购买</div>
		  </div>
		</div>
        )
    }
}

export default Goods